.modal-btn {
  color: #fff;
  background: #3f51b5;
  border: 1px solid #3f51b5;
  box-shadow: 0 5px 10px 0px #3f51b536;
  font-weight: 500;
  font-family: inherit;
  padding: 13px 25px;
  border-radius: 5px;
  position: absolute;
  top: 50%;
  font-size: 18px;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
.sidebar {
  width: 250px;
  padding: 20px;
  border-radius: 0;
  background: #fff;
  height: 100%;
  box-shadow: 0 0 12px 5px #0000000a;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  transform: translateX(-100%);
  transition: all 0.3s linear;
}
.sidebar.show-sidebar {
  transform: translate(0);
}
.sidebar-toggle svg {
  font-size: 24px;
  padding: 10px;
  background: #3f51b538;
  color: #3f51b5;
  border-radius: 8px;
}
.sidebar-toggle {
  border: none;
  background: none;
  margin: 30px 50px;
  cursor: pointer;
}
.sidebar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}
.modal-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: #0000005e;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
}
.modal-overlay.show-modal {
  visibility: visible;
}
.sidebar-header img {
  max-width: 150px;
}
.sidebar-close {
  border: none;
  background: none;
  font-size: 26px;
  color: #f44336;
  cursor: pointer;
}
.social-icons {
  margin-top: 60px;
  display: flex;
  flex-wrap: wrap;
  margin-left: 15px;
}
.social-icons svg {
  font-size: 24px;
  color: #787b8b;
  margin-right: 25px;
}
.modal-container {
  background: #fff;
  width: 400px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 10px;
}
.modal-container h3 {
  color: #3f51b5;
}
.close-modal-btn {
  border: none;
  background: none;
  position: absolute;
  top: 15px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  color: #f44336;
}
.sidebar-links {
  display: flex;
  flex-direction: column;
}
.sidebar-links a {
  display: flex;
  align-items: center;
  color: #87909f;
  text-decoration: none;
  padding: 16px;
  border-radius: 8px;
  font-weight: 500;
  text-transform: capitalize;
  transition: all 0.3s linear;
}
.sidebar-links a:hover {
  background: #3f51b51e;
  color: #3f51b5;
}
.sidebar-links a:hover svg {
  color: #3f51b5;
}
.sidebar-links svg {
  color: #87909f;
  font-size: 25px;
  margin-right: 15px;
  width: 25px;
}
